
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video,
input {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font-weight: normal;
  vertical-align: baseline;
}
html,
body {
  height: 100%;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.25s ease-out;
  transition: opacity 0.25s ease-out;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
/* custom */
a {
  color: #7e8c8d;
  -webkit-backface-visibility: hidden;
  text-decoration: none;
}
li {
  list-style: none;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  width: 1024px;
  height: 100%;
  margin: 0 auto;
}
#app .container {
  height: auto;
  overflow: hidden;
  padding-top: 90px;
}
.page {
  height: auto;
  overflow: hidden;
}
.page .icon_no_list {
  width: 150px;
  height: 30px;
  line-height: 30px;
  margin: 120px auto 0;
  padding-top: 150px;
  background: url(assets/icon_no_list.png) no-repeat top center;
  text-align: center;
}
.page .noted-grid {
  height: auto;
  overflow: hidden;
  margin: 5px 15px;
  border: 1px solid #ccc;
  border-bottom: none;
  border-left: none;
}
.page .noted-grid .grid-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  height: 60px;
  border-bottom: 1px solid #ccc;
}
.page .noted-grid .grid-item > div {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  height: 60px;
  border-left: 1px solid #ccc;
  padding: 5px;
  line-height: 50px;
}
.page .noted-grid .grid-item .item-index {
  -ms-flex-preferred-size: 40px;
      flex-basis: 40px;
}
.page .noted-grid .grid-item .item-index .item-wrap {
  text-align: center;
}
.page .noted-grid .grid-item .item-title {
  -ms-flex-preferred-size: 600px;
      flex-basis: 600px;
}
.page .noted-grid .grid-item .item-title .item-wrap {
  width: 600px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #81cde6;
}
.page .noted-grid .grid-item .item-date {
  -ms-flex-preferred-size: 150px;
      flex-basis: 150px;
}
.page .noted-grid .grid-item .item-date .item-wrap {
  color: #a5a5a5;
}
.page .noted-grid .grid-item .item-action {
  -ms-flex-preferred-size: 150px;
      flex-basis: 150px;
}
.page .noted-grid .grid-item .item-action span {
  color: #e5687f;
  font-size: 12px;
  margin-right: 5px;
}



